	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/explorative-prototyping-71f2ad9d4e41/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/1*t-PnNMQ78_vCBb4t1MA0Iw-fefc6cdde8e69a52e8e66e2b7edd331b-56bef.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 630px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 45.49999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"1 t PnNMQ78 vCBb4t1MA0Iw\"\n        title=\"\"\n        src=\"/static/1*t-PnNMQ78_vCBb4t1MA0Iw-fefc6cdde8e69a52e8e66e2b7edd331b-082ce.png\"\n        srcset=\"/static/1*t-PnNMQ78_vCBb4t1MA0Iw-fefc6cdde8e69a52e8e66e2b7edd331b-c7cb1.png 158w,\n/static/1*t-PnNMQ78_vCBb4t1MA0Iw-fefc6cdde8e69a52e8e66e2b7edd331b-93041.png 315w,\n/static/1*t-PnNMQ78_vCBb4t1MA0Iw-fefc6cdde8e69a52e8e66e2b7edd331b-082ce.png 630w,\n/static/1*t-PnNMQ78_vCBb4t1MA0Iw-fefc6cdde8e69a52e8e66e2b7edd331b-c4006.png 945w,\n/static/1*t-PnNMQ78_vCBb4t1MA0Iw-fefc6cdde8e69a52e8e66e2b7edd331b-56bef.png 1200w\"\n        sizes=\"(max-width: 630px) 100vw, 630px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Your next big idea might very well be your greatest success.</p>\n<h1>Explorative Prototyping</h1>\n<p>I’ve been making a lot of simple prototypes recently. Each one has at least one feature that is unique and uses a technology that is new to me, and each explores an idea that I’ve come up with myself.</p>\n<p>I’m pleased with my prototypes and the fact that I’ve created them. Their success — and my apparent boost in prolificity by creating them — is owed to a few simple concepts;</p>\n<ol>\n<li>Understand the key goals.</li>\n<li>Maintain a narrow scope.</li>\n<li>Set a short timeframe.</li>\n</ol>\n<p>That’s it. But let’s take a few moments to delve deeper into what’s actually meant by those principles.</p>\n<hr>\n<h4>Understand the key goals.</h4>\n<p>The crux of a successful prototype is in setting goals. To spend your time effectively you need to know both the ‘<em>why’</em> and have an idea of the ‘<em>how’</em> before you begin. Sometimes you’ll only know the technology you want to explore (your <em>how</em>), and other times you’ll have a goal (your <em>why</em>) but no idea of <em>how</em> to achieve it. You need to know both before you start because:</p>\n<ul>\n<li>A prototype[the <em>how</em>] without a reason[the <em>why</em>] will inevitably fizzle out to a simple proof-of-concept application.</li>\n<li>A challenge[the <em>why</em>] without a solution[the <em>how</em>] isn’t ready for you to begin working on.</li>\n</ul>\n<p>Start with a single high-level primary goal and a few secondary goals, and admit that some of those secondary goals might have to be sacrificed during development to limit your scope or keep within your time constraints.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/1*EwIpZrNyhssgpFPaFVMzMA-30a8f8ed471bd0cd2bdb75238ecd44a2-a9158.jpeg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 630px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 43.9%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"1 EwIpZrNyhssgpFPaFVMzMA\"\n        title=\"\"\n        src=\"/static/1*EwIpZrNyhssgpFPaFVMzMA-30a8f8ed471bd0cd2bdb75238ecd44a2-af57e.jpeg\"\n        srcset=\"/static/1*EwIpZrNyhssgpFPaFVMzMA-30a8f8ed471bd0cd2bdb75238ecd44a2-847f7.jpeg 158w,\n/static/1*EwIpZrNyhssgpFPaFVMzMA-30a8f8ed471bd0cd2bdb75238ecd44a2-93973.jpeg 315w,\n/static/1*EwIpZrNyhssgpFPaFVMzMA-30a8f8ed471bd0cd2bdb75238ecd44a2-af57e.jpeg 630w,\n/static/1*EwIpZrNyhssgpFPaFVMzMA-30a8f8ed471bd0cd2bdb75238ecd44a2-28cf9.jpeg 945w,\n/static/1*EwIpZrNyhssgpFPaFVMzMA-30a8f8ed471bd0cd2bdb75238ecd44a2-a9158.jpeg 1000w\"\n        sizes=\"(max-width: 630px) 100vw, 630px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Primary goal: Escape. Secondary goal: Build kick-ass Iron Man suit.</p>\n<p>For example, here’s some of my recent project goals:</p>\n<p>Name: Project Stats v1<br>\nPrimary: Encourage Source Control Best Practices in Team<br>\nSecondary: Collect data from Bitbucket<br>\nSecondary: Make data explorable with interactive charts</p>\n<p>Name: Work Resourcing v2<br>\nPrimary: Make weekly resourcing more user friendly<br>\nSecondary: Parse XLS files with Node.js<br>\nSecondary: Generate and send emails with Node.js<br>\nSecondary: Authenticate with Office 365 with Node.js</p>\n<p>Name: Monolithic.js<br>\nPrimary: Make upgrading Javascript to ES6 easier<br>\nSecondary: Build a tool to rewrite/reformat Javascript<br>\nSecondary: Break large legacy JS file into multiple files<br>\nSecondary: Don't break functionality of legacy Javascript<br>\nSecondary: Use rollup.js in build process</p>\n<blockquote>\n<p>Clear goals make it easier to control the scope of your prototype; anything that doesn’t actively contribute to meeting your core goals — however ‘neat’ the feature might be — can be shelved until the prototype is complete.</p>\n</blockquote>\n<hr>\n<h4>Maintain a narrow scope.</h4>\n<p>Although it’s great fun to plan a project with all the bells and whistles, you can actually move faster and explore a more varied set of features if you tackle them in isolation. Essentially, limit your work-in-progress at the conceptual level.</p>\n<p>Your high-level goal is often something that could be solved in lots of different ways, so refine what you’re doing, focus on a single feature at a time, deliver it and <em>then</em> move onto the next. If you don’t you risk scope creep taking over your project, and complexity of everything you’re try to build starts to compromise your ability to finish any of it.</p>\n<blockquote>\n<p>Scope creep is the bane of all software development, exponentially increasing the levels of complexity and timeframes of a project.</p>\n</blockquote>\n<hr>\n<h4>Set a short timeframe.</h4>\n<p>And know how much time you want to spend exploring your idea.</p>\n<p>I limit my prototypes to timeframes measured in hours, not days or weeks. A prototype is a proof of concept. It doesn’t need to be beautifully coded, have unit tests or rock-solid deployment pipelines, it needs to <em>work</em>, and in doing so make some measurable progress towards the initial goals.</p>\n<p><img src=\"/1*56VBnNDs794aN5CzGgjWCg-5a28df73b8e04ce3e4c0d5e56693a2b5.gif\"></p>\n<p>After a few hours’ work you should be in a position to estimate how much longer it’s going to take to finish the first version. If it’s over your initial time budget, seriously consider how you could simplify things to release something in your initial timeframe.</p>\n<p>Of course, timeframes vary depending on what you’re attempting, and by whatever works for you. A few hours a day for a few concurrent days is roughly my attention span for new prototypes. Any longer and finishing the prototype stops being fun and becomes a chore. Nobody likes chores.</p>\n<hr>\n<h4>Examples</h4>\n<p>I didn’t really build my prototypes with the intention of including them in this post, but I feel compelled to link to the publicly-available ones now that I’ve (indirectly) written about them.</p>\n<p><strong>ProjectStats:</strong> <a href=\"https://github.com/psyked/ProjectStats\">https://github.com/psyked/ProjectStats</a><br>\nThe combined result of several prototypes, this has included using RequireJS for the first time, exploring the Bitbucket APIs, using D3.js, using C3.js, creating a Chromecast application, using Flexbox properly and setting up nightly scheduled tasks on a linux box.</p>\n<p>[<img src=\"\"></p>\n<p>ProjectStats, in all its brightly-coloured glory!</p>\n<p><strong>Magic: The Gathering card data:</strong> <a href=\"https://github.com/psyked/MTG-NodeJS\">https://github.com/psyked/MTG-NodeJS</a><br>\nCan I use Node.js to scrape a website and extract data and images from it? Yes, I can!</p>\n<p><strong>DashyTheBoard:</strong> <a href=\"https://github.com/psyked/DashyTheBoard\">https://github.com/psyked/DashyTheBoard</a><br>\nExploring Google Analytics APIs in Node.js, using C3.js for custom charts and experimenting with creating more user-friendly charts than the default Google Analytics ones. Also experimenting with the design principles of Google’s Material UI.</p>\n<h4>Finally</h4>\n<p>When it comes to new technologies, you don’t <em>really</em> know how to properly build something until you’ve built it once already, which is what makes prototyping a great practice for honing your development skills.</p>\n<p>Building a prototype can be an exciting way to explore something new, an opportunity to hone your development skills and a chance to explore an idea that you wouldn’t normally — all at the same time.</p>","frontmatter":{"title":"Explorative Prototyping","date":"May 17, 2016","tags":["Hackathon","Prototyping","Programming"],"url":"https://medium.com/@psyked/explorative-prototyping-71f2ad9d4e41","image":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":2.197802197802198,"src":"/static/1*t-PnNMQ78_vCBb4t1MA0Iw-fefc6cdde8e69a52e8e66e2b7edd331b-7a72d.png","srcSet":"/static/1*t-PnNMQ78_vCBb4t1MA0Iw-fefc6cdde8e69a52e8e66e2b7edd331b-8baea.png 158w,\n/static/1*t-PnNMQ78_vCBb4t1MA0Iw-fefc6cdde8e69a52e8e66e2b7edd331b-69705.png 315w,\n/static/1*t-PnNMQ78_vCBb4t1MA0Iw-fefc6cdde8e69a52e8e66e2b7edd331b-7a72d.png 630w,\n/static/1*t-PnNMQ78_vCBb4t1MA0Iw-fefc6cdde8e69a52e8e66e2b7edd331b-514af.png 945w,\n/static/1*t-PnNMQ78_vCBb4t1MA0Iw-fefc6cdde8e69a52e8e66e2b7edd331b-5e778.png 1200w","sizes":"(max-width: 630px) 100vw, 630px"}}},"description":"I’ve been making a lot of simple prototypes recently."}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/lolbot-or-how-i-learned-to-stop-worrying-and-love-my-mvp-7514ad67a5c9/","tags":["Hackathons","Agile","Development"],"title":"LOLBot or: How I Learned to Stop Worrying and Love my MVP","date":"2015-04-05T23:42:54.971Z"}},"next":{"frontmatter":{"path":"/blog/on-thursday-agile-died-b7c882a648/","tags":["Agile","Waterfall","Developers"],"title":"On Thursday, Agile died.","date":"2016-05-25T07:36:02.250Z"}}}}

